package ai.hypergraph.kaliningraph.parsing

import ai.hypergraph.kaliningraph.formatAsGrid
import ai.hypergraph.kaliningraph.tensor.FreeMatrix
import ai.hypergraph.kaliningraph.types.*

fun Production.pretty():  = LHS + " -> " + RHS.joinToString(" ")
val CFG.pretty: FreeMatrix<> by cache { map { it.pretty() }.formatAsGrid(3) }
//fun CJL.prettyPrint() = cfgs.joinToString("\n\n") { it.prettyPrint() }
fun CFG.prettyPrint():  = pretty.toString()